import{watch}from'vue';import DemoNav from'./components/DemoNav.vue';import{useCurrentTheme}from'../common/iframe-sync';import{config}from'site-mobile-shared';export default{components:{DemoNav},setup(){const theme=useCurrentTheme();watch(theme,(newVal,oldVal)=>{document.documentElement.classList.remove(`van-doc-theme-${oldVal}`);document.documentElement.classList.add(`van-doc-theme-${newVal}`);const{darkModeClass,lightModeClass}=config.site;if(darkModeClass){document.documentElement.classList.toggle(darkModeClass,newVal==='dark');}if(lightModeClass){document.documentElement.classList.toggle(lightModeClass,newVal==='light');}},{immediate:true});}};